body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

* {
  padding: 0;
  margin: 0;
}

body {
  background-image: -webkit-linear-gradient(top left, rgb(37, 37, 250), rgb(247, 62, 62));
  background-image: linear-gradient(to bottom right, rgb(37, 37, 250), rgb(247, 62, 62));
  background-repeat: no-repeat;
  background-attachment: fixed;

  height: 100%;
}

.Navbar {
  background-color: rgb(15, 15, 15);

  padding: 10px;
  text-align: center;
  color: white;
}

.GeolocationHead {
  color: black;
}

.infoGeoLoc{
  margin-top: 1%;
  padding-left: 5%;
  padding-right: 5%;
  font-size: 20px;
  }

.weekCards {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: row;
          flex-direction: row;
}

.hourCards {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: row;
          flex-direction: row;
  

}

.weekSum {
  width: 100%;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-flex-wrap: wrap;
          flex-wrap: wrap;

  border: 2px solid black;
  text-align: center;
}

.hourlyWeather {
  width: 15,5%;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: row;
          flex-direction: row;
  -webkit-flex-wrap: wrap;
          flex-wrap: wrap;
  padding: 2%;
  border: 2px solid black;
  text-align: center;
}

.input {
  text-align: center;
}

ul {
  list-style-type: none;
}

h5 {
  font-size: 25px;
  color: black;
  
}

h3 {
  margin-top: 4%;
  margin-bottom: 1%;
  text-align: center;
}


.footer {
  background-color: rgb(15, 15, 15);
  
  border: 2px solid black;
  text-align: center;
  color: red;
}
 
